<template>
  <!-- 主体 -->
  <div class="main-wrapper">
    <!-- 左侧 -->
    <div class="side-wrapper" >
      <div class="slide-wrapper-tow" style="width: 216px;left:0">
        <!-- 左侧导航 -->
        <nav-bar style="padding-bottom: 80px">
          <Menu ref="rightMenu" width="100%" theme="dark" :accordion="false" @on-select="selectFn" :active-name="$route.path">
            <Menu-item   name="/equipment/authorized">
              <span class="queryMenuEllipsis" >{{ $t('authorized') }}</span>
            </Menu-item>
            <Menu-item   name="/equipment/illegal">
              <span class="queryMenuEllipsis">{{ $t('illegal') }}</span>
            </Menu-item>
          </Menu>
        </nav-bar>
        <!-- 左侧导航 -->
      </div>
    </div>
    <!-- 左侧 /-->
    <!-- 内容部分 -->
    <div class="main-container">
      <container>
        <div class="breadData">
          <!-- 面包屑 -->
          <div class="breadcrumb" >
            <Breadcrumb separator=">">
              <span>{{ $t('path') }}：</span>
              <Breadcrumb-item >
                <!--<Icon type="ios-home-outline"></Icon>-->
                {{ $t('home') }}
              </Breadcrumb-item>
              <Breadcrumb-item v-for='(item,index) in breadcrumb' :key="index">
                <!--<Icon :type="pound"></Icon>-->
                {{ itemTitle(item) }}
              </Breadcrumb-item>
            </Breadcrumb>
          </div>
          <!-- 面包屑 /-->
          <!-- 路由 -->
          <transition name="fade" mode="out-in">
            <router-view ></router-view>
          </transition>
        </div>
        <!-- 路由/ -->
      </container>
    </div>
    <!-- 内容部分 /-->
  </div>
  <!-- 主体 /-->
</template>
<script>
  import NavBar from '@/components/layout/navbar/NavBar'
  import Container from '@/components/layout/container/Container'
  export default {
    name: 'equipment',
    components: {
      NavBar,
      Container
    },
    data () {
      return {
      }
    },
    created () {
      this.$router.push({
        path: this.$store.state.app.equipmentNow
      })
    },
    computed: {
      thirdPath () {
        return '/' + this.$route.path.split('/')[2]
      },
      state () {
        return this.$store.state.app
      },
      breadcrumb: function () {
        let allName = this.$store.state.app.router.currentPageName
        if (allName.search('/') === -1) {
          return [allName]
        } else {
          return allName.split('/')
        }
      }
    },
    watch: {
      'thirdPath' (val) {
        this.$store.commit('CHANGE_EQUIPMENT_NOW', this.$route.path)
      }
    },
    mounted () {
    },
    methods: {
      /**
       * 选择菜单
       */
      selectFn (a) {
        this.$router.push({
          path: a
        })
      },
      /**
       *菜单menu通过i18n编译再返回
       */
      itemTitle (item) {
        return this.$t(item)
      }
    }
  }
</script>



